import React, { useEffect, useState } from "react";
import GoogleMapReact from "google-map-react";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import "dotenv/config";
import { Button, CardContent, FormControlLabel, Grid, TextField, withStyles } from "@material-ui/core";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import Axios from "axios";
import Link from '@material-ui/core/Link';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Radio, { RadioProps } from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import { blue, green } from "@material-ui/core/colors";
import Firebase, { app } from 'firebase';
import Swal from "sweetalert2";
import { Dns } from "@material-ui/icons";
import firebase from "firebase";
import { types } from "util";
import Stepperform from  '../routes/Stepperform';
import { v4 as uuidv4 } from 'uuid';
import { auth } from 'firebase'
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    customButton2: {
      width: "8vw",
      minWidth: "80px",
      height: "3.5vh",
      marginLeft: theme.spacing(1),
      marginTop: theme.spacing(1),
    },
    bottom: {
      display: "flex",
      justifyContent: "flex-end",
      alignItems: "flex-end",
    },
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '56ch',
        marginLeft: 25,
      },

    },
  })
);

const GreenRadio = withStyles({
    root: {
      color: green[400],
      '&$checked': {
        color: green[600],
      },
    },
    checked: {},
  })((props: RadioProps) => <Radio color="default" {...props} />);
  
  const BlueRadio = withStyles({
    root: {
      color: blue[400],
      '&$checked': {
        color: blue[600],
      },
    },
    checked: {},
  })((props: RadioProps) => <Radio color="default" {...props} />);

  function Step1application({
}: any) {


  const [inputs, setInputs] = useState({
    usertype: "",
  });

  const onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const {
      target: { name, value },
    } = event;
    setInputs((prevState) => ({ ...prevState, [name]: value }));

  };
  const classes = useStyles();
  const [usertype, setUsertype] = useState<any>();
  const [fname, setFname] = useState("");
  const [lname, setLname] = useState("");
  const [address, setAddress] = useState("");
  const [email, setEmailadd] = useState("");
  const [uid, setUid] = useState();
  const [token, setToken] = useState( Math.random().toString(36).toUpperCase().substring(4));
  // const [password, setPassword] = useState(uid);
  const [contact, setContact] = useState("");
  const [gender, setGender] = useState("");
  const [users, setUsers] = useState<any>();
  const [dob, setDob] = useState("");
  const [status, setStatus] =useState(0)
  const [open, setOpen] = React.useState(false);


  useEffect(() => { 
    const db = Firebase.firestore()
    

  }, [])
  
  // const handlePw = () =>{
  //    const pw = Math.random().toString(36).substring(4);
  // }

  const onSave = () => {

  
    const db = firebase.firestore()
    // const uid = firebase.auth().uid;
    firebase.auth().createUserWithEmailAndPassword(email, token).then(
      ()=>{
        db.collection('users').doc().set({
          usertype,
          fname,
          lname,
          address,
          email,
          uid: "",
          token,
          contact,
          gender,
          dob,
          status,
          created_at: new Date()
        })
        Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Submitted Successfully!',
            showConfirmButton: false,
            timer: 5000
          })
      }
    )


  };

  

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleClickOpen = () => {
    setOpen(true);
  };



  return (
    <>
    <div>
          <form noValidate autoComplete="off">
          <InputLabel id="demo-simple-select-outlined-label">Select Type of User</InputLabel>                      
          <Select                     
           value={usertype}
           onChange={e => {
             setUsertype(e.target.value);
           }}
          label="Select Type"
          style={{width: 500, color: 'green'}}
          >
          <MenuItem value={0}>
          <em>Select</em>
          </MenuItem>
          <MenuItem value={1}>Guide</MenuItem>
          <MenuItem value={2}>Store owner</MenuItem>
          <MenuItem value={3}>Driver</MenuItem>
          </Select>

          <TextField
          style={{marginTop:'20px', width: '500px'}}
          id="firstname"
          label="First Name"
          variant="outlined"
          value={fname}
          onChange={e => {
            setFname(e.target.value);
          }}
          />
          <TextField
          style={{marginTop:'20px', width: '500px'}}
          id="lastname"
          label="Last Name"
          variant="outlined"
          value={lname}
          onChange={e => {
            setLname(e.target.value);
          }}
          />
          <RadioGroup aria-label="gender" name="gender"  value={gender} onChange={e => {
                          setGender(e.target.value);
                        }}>
          <FormControlLabel value="male" control={<BlueRadio />} label="Male" />
          <FormControlLabel value="female" control={<Radio />} label="Female" />
          <FormControlLabel value="other" control={<GreenRadio />} label="Other" />
          </RadioGroup>

          <TextField
          style={{marginTop:'20px', width: '500px'}}
          id="address"
          label="Address"
          variant="outlined"
          value={address}
                        onChange={e => {
                          setAddress(e.target.value);
                        }}
          />

          <TextField
          style={{marginTop:'20px', width: '500px'}}
          type="email"
          id="emailaddress"
          label="Email Address"
          variant="outlined"
          value={email}
                        onChange={e => {
                          setEmailadd(e.target.value);
                        }}
          />

          <TextField
          style={{marginTop:'20px', width: '500px'}}
          id="contactnumber"
          label="Contact Number"
          variant="outlined"
          value={contact}
          onChange={e => {
            setContact(e.target.value);
          }}
          />

          <TextField
          style={{marginTop:'20px', width: '500px'}}
          id="dob"
          type="date"
          variant="outlined"
          value={dob}
                        onChange={e => {
                          setDob(e.target.value);
                        }}
          />
          <div style={{marginTop: '30px', marginBottom: '30px'}}>
            <Button  color="primary" variant="contained" style={{ width: 500, marginBottom: '10px'}} onClick={onSave}>
            Submit
            </Button>
            
            <Button  color="secondary" autoFocus variant="contained" style={{ width: 500}}>
            Cancel
            </Button> 
          </div>
          </form>

        </div>
    </>
  );
}

export default React.memo(Step1application);



