import React, { useEffect, useState } from "react";
import GoogleMapReact from "google-map-react";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import "dotenv/config";
import { Button, CardContent, FormControl, FormControlLabel, Grid, TextField, withStyles } from "@material-ui/core";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import Axios from "axios";
import Link from '@material-ui/core/Link';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Radio, { RadioProps } from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import { blue, green } from "@material-ui/core/colors";
import Firebase, { app } from 'firebase';
import Swal from "sweetalert2";
import { Dns } from "@material-ui/icons";
import firebase from "firebase";
import { types } from "util";
import Stepperform from  '../routes/Stepperform';
import { v4 as uuidv4 } from 'uuid';
import { auth } from 'firebase'
import Dropzone, {useDropzone} from 'react-dropzone'
import DatePicker from "react-datepicker";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    customButton2: {
      width: "8vw",
      minWidth: "80px",
      height: "3.5vh",
      marginLeft: theme.spacing(1),
      marginTop: theme.spacing(1),
    },
    bottom: {
      display: "flex",
      justifyContent: "flex-end",
      alignItems: "flex-end",
    },
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '56ch',
        marginLeft: 25,
      },

    },
  })
);

const GreenRadio = withStyles({
    root: {
      color: green[400],
      '&$checked': {
        color: green[600],
      },
    },
    checked: {},
  })((props: RadioProps) => <Radio color="default" {...props} />);
  
  const BlueRadio = withStyles({
    root: {
      color: blue[400],
      '&$checked': {
        color: blue[600],
      },
    },
    checked: {},
  })((props: RadioProps) => <Radio color="default" {...props} />);

  function Step2application({
}: any) {


  const [inputs, setInputs] = useState({
    usertype: "",
  });

  const onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const {
      target: { name, value },
    } = event;
    setInputs((prevState) => ({ ...prevState, [name]: value }));

  };
  const classes = useStyles();
  const [usertype, setUsertype] = useState<any>();
  const [fname, setFname] = useState("");
  const [lname, setLname] = useState("");
  const [address, setAddress] = useState("");
  const [email, setEmailadd] = useState("");
  const [uid, setUid] = useState();
  const [token, setToken] = useState( Math.random().toString(36).toUpperCase().substring(4));
  // const [password, setPassword] = useState(uid);
  const [contact, setContact] = useState("");
  const [gender, setGender] = useState("");
  const [users, setUsers] = useState<any>();
  const [dob, setDob] = useState("");
  const [status, setStatus] =useState(0)
  const [open, setOpen] = React.useState(false);


  useEffect(() => { 
    const db = Firebase.firestore()
    

  }, [])
  
  // const handlePw = () =>{
  //    const pw = Math.random().toString(36).substring(4);
  // }

  const onSave = () => {

  
    const db = firebase.firestore()
    // const uid = firebase.auth().uid;
    firebase.auth().createUserWithEmailAndPassword(email, token).then(
      ()=>{
        db.collection('users').doc().set({
          usertype,
          fname,
          lname,
          address,
          email,
          uid: "",
          token,
          contact,
          gender,
          dob,
          status,
          created_at: new Date()
        })
        Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Submitted Successfully!',
            showConfirmButton: false,
            timer: 5000
          })
      }
    )


  };

  

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleClickOpen = () => {
    setOpen(true);
  };
  const [startDate, setStartDate] = useState<any>(new Date());
  const [selectedValue, setSelectedValue] = React.useState('a');

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedValue(event.target.value);
  };
  // const getUploadParams = () => {
  //   return { url: 'https://httpbin.org/post' }
  // }
  // const handleChangeStatus = ({ meta }: any, status: any) => {
  //   console.log(status, meta)
  // }

  // const handleSubmit = (files: any[], allFiles: any[]) => {
  //   console.log(files.map(f => f.meta))
  //   allFiles.forEach(f => f.remove())
  // }
  return (
    <>
    <div>
        <Grid container className={classes.root} spacing={0}>
        <Grid item xs={12}>
        <Grid container >
        <div>
        {/* <Dropzone
            getUploadParams={getUploadParams}
            onChangeStatus={handleChangeStatus}
            onSubmit={handleSubmit}
            style={{ dropzone: { minHeight: 200, maxHeight: 250 } }}
          /> */}
            <FormControl variant="outlined" >
              <InputLabel htmlFor="outlined-age-native-simple">Select Type of User</InputLabel>
                <Select      
                  value={usertype}
                  onChange={e => {
                    setUsertype(e.target.value);
                  }}
                  label="Select Type"
                  style={{color: 'green' , maxWidth: 450}}
                >
                  <MenuItem value={0}>
                  <em>Select</em>
                  </MenuItem>
                  <MenuItem value={1}>Guide</MenuItem>
                  <MenuItem value={2}>Store owner</MenuItem>
                  <MenuItem value={3}>Driver</MenuItem>
                </Select>
                <br/>
              <div style={{backgroundColor: '#D3D3D3', marginRight: 10}}>
                <TextField
                  style={{marginTop:'5px',  width: 450}}
                  id="firstname"
                  label="First Name"
                  variant="outlined"
                  value={fname}
                  onChange={e => {
                    setFname(e.target.value);
                  }}
                />
                <TextField
                  style={{marginTop:'20px',  width: 450}}
                  id="lastname"
                  label="Last Name"
                  variant="outlined"
                  value={lname}
                  onChange={e => {
                    setLname(e.target.value);
                  }}
                />
               

                  <Select
                    label="year"
                    style={{width:125, marginTop: 20, margin: 10, marginLeft:10}}
                  >
                  </Select>
                  <Select
                    label="year"
                    style={{width:125 ,margin:10, marginLeft: 18}}
                  >
                  </Select>
                  <Select
                    label="year"
                    style={{width:125, margin:10, marginLeft: 20}}
                  >
                  </Select>
                    <div style={{color:'black'}}>
                      Gender
                      <BlueRadio
                        checked={selectedValue === 'a'}
                        value='a'
                        name="radio-button-demo"
                        inputProps={{ 'aria-label': 'A' }}
                      />
                      Male
                      
                      <Radio
                        checked={selectedValue === 'b'}
                        value='b'
                        name="radio-button-demo"
                        inputProps={{ 'aria-label': 'B' }}
                        
                      />
                      Female
                    </div>
                  </div>

                  <div style={{backgroundColor: '	#D3D3D3', marginTop: 10, marginRight: 10}}>

                  <TextField
                    style={{marginTop:'30px',  width: 450}}
                    id="address"
                    label="address"
                    variant="outlined"
                    value={address}
                    onChange={e => {
                      setAddress(e.target.value);
                    }}
                  />

                  <TextField
                    style={{marginTop:'20px', width: 450}}
                    type="email"
                    id="emailaddress"
                    label="Email Address"
                    variant="outlined"
                    value={email}
                                  onChange={e => {
                                    setEmailadd(e.target.value);
                                  }}
                    />

                    <TextField
                    style={{marginTop:'20px', width: 450}}
                    id="contactnumber"
                    label="Contact Number"
                    variant="outlined"
                    value={contact}
                    onChange={e => {
                      setContact(e.target.value);
                    }}
                    />
                    <DatePicker
                      selected={startDate}
                      onChange={date => setStartDate(date)}
                      showYearPicker
                      dateFormat="yyyy"
                      // yearItemNumber={9}
                    />

                  </div>
                
                {/* <RadioGroup aria-label="gender" name="gender"  value={gender} onChange={e => {
                              setGender(e.target.value);
                            }}>
                <FormControlLabel value="male" control={<BlueRadio />} label="Male" />
                <FormControlLabel value="female" control={<Radio />} label="Female" />
                <FormControlLabel value="other" control={<GreenRadio />} label="Other" />
                </RadioGroup>
                 */}
              
            
              <div style={{marginTop: '30px', marginBottom: '30px'}}>
              <Button  color="primary" variant="contained" style={{ width: 450, marginBottom: '10px'}} onClick={onSave}>
                Submit
              </Button>
              
              <Button  color="secondary" autoFocus variant="contained" style={{ width:450}}>
                Cancel
              </Button> 
            </div>

          </FormControl>
          

        </div>

        </Grid>
        </Grid>
        </Grid>

    </div>
    </>
  );
}

export default React.memo(Step2application);



